import React from 'react';
import Sets from './pages/sets'
import Layers from './pages/layers'
import LayerItems from './pages/layer-items/LayerItems'
import LayerItemsAdd from './pages/layer-items/LayerItemsAdd'
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';


import "react-tabs/style/react-tabs.css";
import './App.scss';
import { connect } from 'react-redux'


export default class App extends React.Component {

  render() {
    return (
      <div className="App">
        <AppBar position="static">
          <Toolbar>
            <IconButton color="inherit" aria-label="Menu">
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" color="inherit">
              Avatar Platform Admin Panel
            </Typography>
            {/*<Button color="inherit">Login</Button>*/}
          </Toolbar>
        </AppBar>
        <Router basename='/avatar-platform-admin'>
          <Switch>
            <Route path="/" exact component={Sets} />
            <Route path="/:setName" exact component={Layers} />
            <Route path="/:setName/add" component={LayerItemsAdd} />
            <Route path="/:setName/:id" component={LayerItems} />
          </Switch>
        </Router>
        <footer className="App-footer">
          <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
             Some Copyright
          </Typography>
        </footer>
      </div>
    );
  }
}

